nav {
    position: fixed;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
    text-align: right;
    z-index: 100;
    display: none;
}

nav button {
    line-height: 25px;
    position: relative;
    padding-right: 20px;
    color: #707070;
    background: none;
    border: none;
    outline: none;
    font-family: "Roboto Mono", monospace;
    font-weight: 500;
}

nav button:hover .nav-label {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
}

nav .nav-dot {
    display: block;
    width: 10px;
    height: 10px;
    position: absolute;
    right: 0;
    top: 55%;
    background-color: #707070;
    border-radius: 100%;
    transform: translateY(-50%);
    transition: all 0.1s linear;
}

nav .nav-label {
    display: block;
    opacity: 0.5;
    visibility: hidden;
    transform: translateX(-10px);
    transition: all 0.2s cubic-bezier(0.17, 0.67, 0.83, 0.67);
}

.is-dark {
    color: #000000;
}

.is-dark .nav-dot {
    background-color: rgba(0, 0, 0);
}

nav ul {
    margin: 0;
    padding: 0;
}

nav li {
    width: auto;
    list-style: none;
}
