@font-face {
    font-family: "Roboto Mono";
    font-style: normal;
    font-weight: 700;
    font-display: swap;
    src: local("Roboto Mono"), local("RobotoMono"),
        url("./assest/fonts/RobotoMono-Bold.ttf");
}

@font-face {
    font-family: "Roboto Mono";
    font-style: normal;
    font-weight: 300;
    font-display: swap;
    src: local("Roboto Mono"), local("RobotoMono"),
        url("./assest/fonts/RobotoMono-Light.ttf");
}
@font-face {
    font-family: "Roboto Mono";
    font-style: normal;
    font-weight: 500;
    font-display: swap;
    src: local("Roboto Mono"), local("RobotoMono"),
        url("./assest/fonts/RobotoMono-Medium.ttf");
}
@font-face {
    font-family: "Roboto Mono";
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local("Roboto Mono"), local("RobotoMono"),
        url("./assest/fonts/RobotoMono-Regular.ttf");
}
@font-face {
    font-family: "Roboto Mono";
    font-style: normal;
    font-weight: 600;
    font-display: swap;
    src: local("Roboto Mono"), local("RobotoMono"),
        url("./assest/fonts/RobotoMono-SemiBold.ttf");
}

* {
    margin: 0;
    padding: 0;
}

.section {
    background: #fafafa;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    font-family: "Roboto Mono", monospace;
}

/* Media */
@media (min-width: 600px) {
    /* Home */
    .home__greeting {
        font-size: 1.5em;
    }
    .home__name {
        font-size: 3.1em;
    }
    .home__description {
        font-size: 1.3em;
    }
    /* About */
    .about__header {
        font-size: 1.5em;
    }
    .about__experience {
        margin: 0;
        display: flex;
    }
    .about__timeline {
        margin: 10px 0;
    }
    .about__content {
        padding-left: 15px;
    }
    .about__date {
        min-width: 120px;
        padding-right: 15px;
        padding-top: 5px;
        font-size: 0.7em;
        width: 120px;
        border-right: 2px solid #ffca28;
    }
    .about__date::before {
        content: "";
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: #ffca28;
        position: absolute;
        top: 13px;
        right: -6px;
    }
    .about__company {
        padding-top: 5px;
        font-size: 1.1em;
    }
    .about__list {
        font-size: 0.8em;
    }
    .about__section {
        font-size: 0.85em;
    }
    /* Contacts */
    .contacts__link {
        font-size: 0.8em;
        padding: 15px 20px;
    }
    .contacts__icon {
        width: 28px;
    }
}

@media (min-width: 960px) {
    /* Nav */
    nav {
        display: block;
    }
    /* Home */
    .home__greeting {
        font-size: 1.7em;
    }
    .home__name {
        font-size: 3.7em;
    }
    .home__description {
        font-size: 1.5em;
    }
    /* About */
    .about__header {
        font-size: 1.7em;
    }
    .about__date {
        font-size: 0.8em;
        width: 150px;
    }
    .about__date::before {
        width: 12px;
        height: 12px;
        top: 14px;
        right: -7px;
    }
    .about__company {
        font-size: 1.2em;
    }
    .about__list {
        font-size: 0.9em;
    }
    .about__section {
        font-size: 1em;
    }
    /* Contacts */
    .contacts__link {
        font-size: 1em;
    }
    .contacts__icon {
        width: 32px;
        height: 32px;
    }
}

@media (min-width: 1280px) {
        /* Nav */
        nav .nav-dot {
            height: 0.7em;
            width: 0.7em;
        }
        nav button {
            font-size: 0.9em;
        }
    /* Home */
    .home__greeting {
        font-size: 1.9em;
    }
    .home__name {
        font-size: 4em;
    }
    .home__description {
        font-size: 1.6em;
    }
    /* About */
    .about__header {
        font-size: 1.9em;
    }
    .about__date {
        padding-top: 15px;
        font-size: 0.9em;
        width: 15%;
    }
    .about__date::before {
        top: 20px;
    }
    .about__company {
        padding-top: 10px;
        font-size: 1.4em;
    }
    .about__list {
        font-size: 1em;
    }
    .about__section {
        padding-top: 5px;
        font-size: 1.1em;
    }
    /* Contacts */
    .contacts__link {
        font-size: 1em;
    }
    .contacts__icon {
        width: 32px;
        height: 32px;
    }
}

@media (min-width: 1920px) {

    /* Home */
    .home__greeting {
        font-size: 2.1em;
    }
    .home__name {
        font-size: 4.5em;
    }
    .home__description {
        font-size: 2em;
    }
    /* About */
    .about__header {
        font-size: 2.1em;
    }
    .about__date {
        padding-left: 20px;
        padding-top: 18px;
        font-size: 1.1em;
        border-right: 3px solid #ffca28;
    }
    .about__date::before {
        width: 14px;
        height: 14px;
        top: 25px;
        right: -8px;
    }
    .about__company {
        padding-top: 10px;
        font-size: 1.7em;
    }
    .about__list {
        font-size: 1.2em;
    }
    .about__section {
        padding-top: 10px;
        font-size: 1.3em;
    }
    /* Contacts */
    .contacts__link {
        padding: 20px 30px;
        font-size: 1.3em;
    }
    .contacts__icon {
        width: 45px;
        height: 45px;
    }
}
