.about {
    flex: 1;
    padding: 10px;
    box-sizing: border-box;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.about__header {
    color: #ffca28;
    text-align: center;
    font-size: 1.1em;
    letter-spacing: 2px;
    text-shadow: 0.5px 0.5px 1px #212121;
}

.about__timeline {
    display: flex;
    flex-direction: column;
    flex: 1;
}

.about__experience {
    color: #212121;
    margin-top: 5px;
}

.about__experience:last-child {
    flex: 1;
}

.about__date {
    position: relative;
    text-align: right;
    font-size: 0.6em;
    font-style: italic;
}

.about__company {
    padding-bottom: 2px;
    font-size: 0.9em;
    font-style: italic;
}

.about__list {
    list-style: none;
    font-size: 0.65em;
}

.about__section {
    padding-top: 3px;
    font-size: 0.7em;
    padding-bottom: 1px;
}
