.home {
    height: 100vh;
    overflow: visible;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
}

.home__greeting {
    font-size: 1.1em;
}

.home__name {
    padding: 19px;
    font-weight: 900;
    font-size: 2.5em;
    letter-spacing: 2px;
    line-height: 1;
    color: #ffca28;
    text-transform: uppercase;
    text-shadow: 0.5px 0.5px 1px #212121;
}

.home__description {
    font-size: 1em;
    letter-spacing: 1px;
}

.home__greeting,
.home__name,
.home__description {
    font-weight: 500;
}
