.contacts {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contacts__content {
    display: flex;
    flex-direction: column;
}

.contacts__link {
    position: relative;
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #212121;
    font-size: 0.8em;
    padding: 5px 10px;
}

.contacts__link:hover {
    box-sizing: border-box;
    box-shadow: 0 0 0 2px #ffca28;
    background: #f0f0f0;
}

.contacts__icon {
    width: 24px;
}

.contacts__text {
    margin-left: 10px;
}
